<template>
  <div class="sale-card">
    <div class="card" @dblclick="finish(sale)" v-if="sale.finishedAt" style="background-color: lightgreen;">
      <div class="card-content">
        <h2 class="is-size-4 has-text-weight-bold">{{ sale.user.name }}</h2>
        <ul>
            <li v-for="(product, index) in sale.products" :key="product._id" :index="index" >
               <div v-if="product.quantity > 0">
                {{ product.description + ' => ' + product.quantity }}
              </div>
            </li>
        </ul> 
        <p>Total => {{sale.total}}</p>
      </div>
    </div>
    <div v-else class="card" @dblclick="finish(sale)">
      <div class="card-content">
        <h2 class="is-size-4 has-text-weight-bold">{{ sale.user.name }}</h2>
        <ul>
            <li v-for="(product, index) in sale.products" :key="product._id" :index="index" >
              <div v-if="product.quantity > 0">
                {{ product.description + ' => ' + product.quantity }}
              </div>
            </li>
        </ul> 
        <p>Total => {{sale.total}}</p>
      </div>
    </div>
  </div>
</template>
<script>
//import { ref } from 'vue';
import saleService from '@/services/SalesService.js';
export default {
  name: 'SaleCard',
  props: { sale: {type: Object} },//[ 'sale' ],
  setup(){
    async function finish(sale) {
      //console.log(sale)
      sale.finishedAt = Date();
      saleService.update(sale._id, sale)
        .then(response => {
            console.log(response.data)
        })
        .catch(e => {
            console.log(e);
        }); 
        sale.value = {user: {}, products: []};   
    }
    return {
      finish
    }
  }
}
</script>
