<template>
  <section>
    <div class="container">
      <h1 class="title">Report</h1>
      <div class="post-form">
        <div class="field">
          <div class="control">
            <input v-model="production.createdAt" class="input" type="text" placeholder="Inicio">
          </div>
        </div>
        <div class="field">
          <div class="control">
            <input v-model="production.finishAt" class="input" type="text" placeholder="Fim">
          </div>
        </div>     
      </div>
      <br>
      <br>
      <div class="columns">
        <ul class="block-list is-small is-outlined is-success is-centered has-radius is-info">
          <div class="item" v-for="(production, index) in productionList" 
          :key="production.id"
          :index="index"
          @click="setProduction(index)">
              <li v-if="!production.finishedAt.length" class="text">
                <p>{{ production.createdAt }}</p>
                <p v-for="(sales) in production.sales" :key="sales._id">{{ sales._id + " => " + sales.total }}</p>
              </li>
              <li v-else style="background-color: lightcoral;" class="text">
                <p>{{ production.finishAt }}</p>
                <p v-for="(sales) in production.sales" :key="sales._id">{{ sales._id + " => " + sales.total }}</p>
              </li>
          </div>
        </ul>
      </div>
    </div>
  </section>
</template>

<script>
import { ref } from 'vue';
import reportService from '@/services/ReportService.js';

export default {
  name: 'Production',
  setup() {
    const productionList = ref([]);
    const production = ref({});
    var modal = ref(false);

   
    async function getProduction() {
      reportService.getAll(production)
        .then(response => {
          productionList.value = response.data;
          console.log(response.data);
        })
        .catch(e => {
          console.log(e);
        });
    }
    getProduction();

    return {
      productionList,
      production,
      modal
    }
  }
}
</script>