<template>
  <section>
    <div class="container">
      <h1 class="title">
        Produção 
      </h1>
      <div class="post-form">
        <div class="field">
          <div class="control">
            <input v-model="production._id" class="input" type="text" placeholder="Id">
          </div>
        </div>
        <div class="field">
          <div class="control">
            <input v-model="recipeAdd.description" class="input" type="text" placeholder="Produto" autocomplete="off" @input="filterItems" @focus="modal = true">
            <div v-if="filteredRecipes && modal">
                <ul>
                    <li class="box" v-for="filteredRecipe in filteredRecipes" :key="filteredRecipe._id" @click="setRecipe(filteredRecipe)">{{ filteredRecipe.description }}</li>
                </ul>
            </div>
          </div>
        </div>
        <div class="field">
          <div class="control">
            <input v-model="recipeAdd.quantity" class="input" type="text" placeholder="Quantidade">
          </div>
        </div>
        <div class="control">
          <button v-on:click="insertRecipe()" class="button is-link">Adicionar</button>
        </div>
        <ul>
            <li v-for="(product, index) in productList" :key="product._id" :index="index" @click="setRecipe(product)" @dblclick="deleteProduct(index)">{{ product.description + ' => ' + product.quantity }}</li>
        </ul>        
      </div>
      <br>
      <div class="field is-grouped">
        <div class="control">
          <button v-on:click="postProduction()" class="button is-link">Salvar</button>
        </div>
        <div class="control">
          <button v-on:click="recipe = []" class="button is-link is-light">Cancel</button>
        </div>
         <div class="control">
          <button v-on:click="deleteProduction()" class="button is-danger">Delete</button>
        </div>
         <div class="control">
          <button v-on:click="closeProduction()" class="button is-danger">Close</button>
        </div>
        <div class="control">
          <button v-on:click="reopenProduction()" class="button is-danger">Reopen</button>
        </div>
      </div>

      <br>
      <div class="columns">
        <ul class="block-list is-small is-outlined is-success is-centered has-radius is-info">
          <div class="item" v-for="(production, index) in productionList" 
          :key="production.id"
          :index="index"
          @click="setProduction(index)">
              <li v-if="!production.finishedAt.length" class="text">
                <p>{{ production.createdAt }}</p>
                <p v-for="(sales) in production.sales" :key="sales._id">{{ sales._id + " => " + sales.total }}</p>
              </li>
              <li v-else style="background-color: lightcoral;" class="text">
                <p>{{ production.finishedAt }}</p>
                <p v-for="(sales) in production.sales" :key="sales._id">{{ sales._id + " => " + sales.total }}</p>
              </li>
          </div>
        </ul>
      </div>
    </div>
    
   
  </section>
</template>

<script>
import { ref } from 'vue';
import recipesService from '@/services/RecipesService.js';
import productionService from '@/services/ProductionService.js';

export default {
  name: 'Production',
  setup() {
    const recipe = ref([]);
    const production = ref({});
    const recipesList = ref([]);
    const filteredRecipes = ref([]);
    const recipeAdd = ref ({});
    const productList = ref([]);
    const productionList = ref([]);
    var modal = ref(false);

    function closeProduction(){
      production.value.finishedAt = Date();
      postProduction();
    }

    function reopenProduction(){
      production.value.finishedAt = {};
      postProduction();
    }

    function deleteProduct(index){
        productList.value.splice(index, 1);
    }

    function insertRecipe(){
      if(!production.value.cost && !production.value.total){
        production.value.cost = 0;
        production.value.total = 0;
      }
      production.value.cost += recipeAdd.value.cost;
      production.value.total += (recipeAdd.value.price * recipeAdd.value.quantity);
      if(productList.value.length != 0){
        if(!productList.value.some(function(p) {if(p._id === recipeAdd.value._id) {
                                                                        //console.log(p.quantity = parseInt(p.quantity) + parseInt(recipeAdd.value.quantity))
                                                                        p.quantity = parseInt(p.quantity) + parseInt(recipeAdd.value.quantity)
                                                                        return true;
                                                                      } }))
                                                                      productList.value.push(recipeAdd.value);

      }
      else
        productList.value.push(recipeAdd.value);

      recipeAdd.value = {};
    }

    function filterItems(){
        if(recipeAdd.value.description != ''){
            filteredRecipes.value = recipesList.value.filter(items => {
                return items.description.toLowerCase().startsWith(recipeAdd.value.description.toLowerCase())
            });
        } else{
            filteredRecipes.value = [];
        }
    }

    function setRecipe(recipe){
      recipeAdd.value._id = recipe._id;
      recipeAdd.value.description = recipe.description;
      recipeAdd.value.cost = recipe.cost;
      recipeAdd.value.price = recipe.price;
      recipeAdd.value.quantity = recipe.quantity;
      filteredRecipes.value = [];
    }

    async function getAllRecipes() {
      recipesService.getAll()
        .then(response => {
          recipesList.value = response.data;
          //console.log(response.data);
        })
        .catch(e => {
          console.log(e);
        });
    }
    getAllRecipes();

    async function postProduction() {
      if(!production.value._id){
        production.value.products = productList.value;
        console.log(production.value)
        productionService.create(production.value)
        .then(response => {
            productionList.value = response.data;
        })
        .catch(e => {
            console.log(e);
        });
      } else{
        production.value.products = productList.value;
        productionService.update(production.value._id, production.value)
        .then(response => {
            console.log(response.data)
        })
        .catch(e => {
            console.log(e);
        });
      }
      production.value = [];   
      productList.value = [];
          
    }

    function setProduction(index){
      production.value = productionList.value[index];
      productList.value = production.value.products;
      modal = false;
    }
    
    async function getProduction() {
      productionService.getAll()
        .then(response => {
          productionList.value = response.data;
          console.log(response.data);
        })
        .catch(e => {
          console.log(e);
        });
    }
    getProduction();

    async function deleteProduction() {
      productionService.delete(production.value._id)
        .then(response => {
          productionList.value = response.data;
        })
        .catch(e => {
          console.log(e);
        });
        production.value = {}; 
        productList.value = [];
    }


    return {
      production,
      recipesList,
      recipe,
      setRecipe,
      postProduction,
      filteredRecipes,
      filterItems,
      recipeAdd,
      setProduction,
      insertRecipe,
      deleteProduct,
      productList,
      productionList,
      deleteProduction,
      closeProduction,
      reopenProduction,
      modal
    }
  }
}
</script>