<template>
<nav class="navbar container" role="navigation" aria-label="main navigation">
  <div class="navbar-brand">
    <router-link to="/" class="navbar-item"><strong class="is-size-4">Salty Bear</strong></router-link>
    <!-- <a class="navbar-item" href="/">
      <strong class="is-size-4">Salty Bear</strong>
    </a> -->
    <a role="button" class="navbar-burger burger" aria-label="menu" aria-expanded="false" data-target="navbarBasicExample">
      <span aria-hidden="true"></span>
      <span aria-hidden="true"></span>
      <span aria-hidden="true"></span>
    </a>
  </div>
  <div id="navbar" class="navbar-menu">
    <div class="navbar-start">
      <router-link to="/" class="navbar-item">Home</router-link>
      <router-link to="/Items" class="navbar-item">Items</router-link>
      <router-link to="/Recipes" class="navbar-item">Receitas</router-link>
      <router-link to="/Sales" class="navbar-item">Vendas</router-link>
      <router-link to="/Production" class="navbar-item">Produção</router-link>
      <router-link to="/Users" class="navbar-item">Cliente</router-link>
      <router-link to="/Debtors" class="navbar-item">Devedores</router-link>
      <router-link to="/Report" class="navbar-item">Relatório</router-link>
    </div>
   <div class="navbar-end">
    <div class="navbar-item">
      <div class="buttons">
        <!-- Check that the SDK client is not currently loading before accessing is methods -->
        <div v-if="userAux == ''">
          <!-- show login when not authenticated -->
          <router-link to="/Login" v-if="!userAux" class="button is-dark"><strong>{{ userAux }}Sign in</strong></router-link>
        </div>
        <div v-else>
          <!-- show logout when authenticated -->
          <a v-if="userAux" @click="Logout" class="button is-dark"><strong>{{ userAux }} => Log out</strong></a>
        </div>
      </div>
    </div>
  </div>
  </div>
