import http from "../http-common";

class SalesService {
  getAll(data) {
    return http.get("/sales", {params: data});
  }

  get(id) {
    return http.get(`/sales/${id}`);
  }

  create(data) {
    return http.post("/sales", data);
  }

  update(id, data) {
    return http.put(`/sales/${id}`, data);
  }

  delete(id, productionId) {
    return http.delete(`/sales/${id}/${productionId}`);
  }

  deleteAll() {
    return http.delete(`/sales`);
  }
}

export default new SalesService();