import http from "../http-common";

class ItemsService {
  getAll() {
    return http.get("/items");
  }

  get(id) {
    return http.get(`/items/${id}`);
  }

  create(data) {
    const item = { description: data.description.trim(), quantity: data.quantity.trim(), price: data.price.trim() }
    return http.post("/items", item);
  }

  update(id, data) {
    return http.put(`/items/${id}`, data);
  }

  delete(id) {
    return http.delete(`/items/${id}`);
  }

  deleteAll() {
    return http.delete(`/items`);
  }
}

export default new ItemsService();