import http from "../http-common";

class RecipesService {
  getAll() {
    return http.get("/recipes");
  }

  get(id) {
    return http.get(`/recipes/${id}`);
  }

  create(data) {
    const recipe = { description: data.description.trim(), cost: data.cost, quantity: data.quantity.trim(), price: data.price.trim(), directions: data.directions.trim(), ingredients: data.ingredients }
    return http.post("/recipes", recipe);
  }

  update(id, data) {
    return http.put(`/recipes/${id}`, data);
  }

  delete(id) {
    return http.delete(`/recipes/${id}`);
  }

  deleteAll() {
    return http.delete(`/recipes`);
  }
}

export default new RecipesService();