<template>
  <section>
    <div class="container">
      <h1 class="title">
        Ingredientes
      </h1>
      <div class="post-form">
        <div class="field">
          <div class="control">
            <input v-model="selectedItem._id" class="input" type="text" placeholder="Id">
          </div>
        </div>
        <div class="field">
          <div class="control">
            <input v-model="selectedItem.description" class="input" type="text" placeholder="Descrição">
          </div>
        </div>
        <div class="field">
          <div class="control">
            <input v-model="selectedItem.quantity" class="input" type="text" placeholder="Quantidade">
          </div>
        </div>
        <div class="field">
          <div class="control">
            <input v-model="selectedItem.price" class="input" type="text" placeholder="Preço">
          </div>
        </div>
      </div>
      <br>
      <div class="field is-grouped">
        <div class="control">
          <button v-on:click="postItem()" class="button is-link">Submit</button>
        </div>
        <div class="control">
          <button v-on:click="selectedItem = []" class="button is-link is-light">Cancel</button>
        </div>
         <div class="control">
          <button v-on:click="deleteItem()" class="button is-danger">Delete</button>
        </div>
      </div>

      <br>
      <div class="columns is-multiline">
        <div class="item column is-one-quarter" v-for="(item, index) in itemsList" :key="item.id" :index="index" @click="getItem(index)">
          <!-- <li class="text">{{ item.description }}</li> -->
          <div class="card">
            <div class="card-content">
              <h2 class="is-size-4 has-text-weight-bold">{{ item.description }}</h2>
            </div>
          </div>
        </div>
      </div>
    </div>
    
   
  </section>
</template>

<script>
import { ref } from 'vue';
import ItemsService from '@/services/ItemsService.js';


export default {
  name: 'Items',
  setup() {
    const itemsList = ref([]);
    const selectedItem = ref([]);

    async function getAllItems() {
      ItemsService.getAll()
        .then(response => {
          itemsList.value = response.data;
          //console.log(response.data);
        })
        .catch(e => {
          console.log(e);
        });
    }
    getAllItems();

    function getItem(index){
      selectedItem.value = itemsList.value[index];
    }

    async function postItem() {
      if(selectedItem.value.description.trim() && selectedItem.value.quantity.trim() && selectedItem.value.price.trim()){
        if(!selectedItem.value._id){
          ItemsService.create(selectedItem.value)
          .then(response => {
            itemsList.value = response.data;
          })
          .catch(e => {
            console.log(e);
          });
        } else{
          ItemsService.update(selectedItem.value._id, selectedItem.value)
          .then(response => {
            console.log(response.data)
          })
          .catch(e => {
            console.log(e);
          });
        }
        selectedItem.value = [];   
      } else{
        alert('Fudeu')
      }
           
    }

    async function deleteItem() {
      ItemsService.delete(selectedItem.value._id)
        .then(response => {
          itemsList.value = response.data;
        })
        .catch(e => {
          console.log(e);
        });
        selectedItem.value = []; 
    }

    return {
      itemsList,
      selectedItem,
      getItem,
      postItem,
      deleteItem
    }
  },
  methods:{
    // async getItemsData() {
    //   itemsList = ItemsService.getItemsData()
