    //     body: JSON.stringify(selectedItem)
    //   };
    //   const response = await fetch(`${GENERATOR_BASE}/api/items`, requestOptions);
    //   const data = await response.json();
    //   this.postId = data.id;
    // },
    // getItem(index){
    //   if(itemsList[index] != undefined)
    //     selectedItem.value.push(itemsList.value[index]);
    //     console.log(selectedItem.value);
    // }
    
  }
}
</script>

