<template>
  <section>
    <div class="container">
      <h1 class="title">
        Vendas 
      </h1>
      <div class="post-form">
        <div class="field">
          <div class="control">
            <input v-model="sale._id" class="input" type="text" placeholder="Id">
          </div>
        </div>
        <div class="field">
          <div class="control">
            <input v-model="sale.user.name" class="input" type="text" placeholder="Cliente" autocomplete="off" @input="filterUsers" @focus="modal = true">
            <div v-if="filteredUsers && modal">
                <ul>
                    <li class="box"  v-for="filteredUser in filteredUsers" :key="filteredUser._id" @click="selectedUser(filteredUser)">{{ filteredUser.name }}</li>
                </ul>
            </div>
          </div>
        </div> 
        
          <div class="button" v-for="product in productList" :key="product._id">
            <span>{{ product.description }}</span>
            <span class="icon">
              <i @click="selectedProduct(product, true)" class="fas fa-plus"></i>
            </span>
            <span v-if="!product.quantityAux">{{ product.quantityAux = 0 }}</span>
            <span v-else>{{ product.quantityAux }}</span>
            <span class="icon">
              <i @click="selectedProduct(product, false)" class="fas fa-minus"></i>
            </span>
            <span>{{ product.quantity }}</span>
            
          </div>
      </div>

      <br>
      <div class="field is-grouped">
        <div class="control">
          <button v-on:click="postsale()" class="button is-link">Salvar</button>
        </div>
        <div class="control">
          <button v-on:click="cancel()" class="button is-link is-light">Cancel</button>
        </div>
         <div class="control">
          <button v-on:click="deletesale()" class="button is-danger">Delete</button>
        </div>
      </div>
      

      <br>
      <div class="columns is-multiline">
         <div class="column is-one-quarter" v-for="(sale, index) in saleList" 
          :key="sale.id"
          :index="index"
          @click="setsale(index)">
              <SaleCard :sale="sale" />
              <!-- <li class="text">{{ sale.createdAt + ' => ' + index }}</li> -->
          </div>
      </div>
    </div>
  </section>
</template>

<script>
import { ref } from 'vue';
import productionService from '@/services/ProductionService.js';
import saleService from '@/services/SalesService.js';
import usersService from '@/services/UsersService.js'
import SaleCard from '@/components/SaleCard.vue';
import firebase from 'firebase/app';
import 'firebase/auth';



export default {
  name: 'sale',
  components : {
    SaleCard
  },
  mounted: function () {
    // var dropdown = document.querySelector('.dropdown');
    // dropdown.addEventListener('click', function(event) {
    //   event.stopPropagation();
    //   dropdown.classList.toggle('is-active');
    // });
  },
  setup() {
    const sale = ref({
      user: {},
      products: []
    });
    const usersList = ([]);
    const filteredUsers = ref([]);
    const product = ref({});
    const productList = ref([]);
    const saleList = ref([]);
    const production = ref({});
    var modal = ref(false);
    

    //Users
    async function getAllUsers() {
      usersService.getAll()
        .then(response => {
          usersList.value = response.data;
        })
        .catch(e => {
          console.log(e);
        });
    }
    getAllUsers()

    function filterUsers(){
        if(sale.value.user.name != ''){
            filteredUsers.value = usersList.value.filter(users => {
                return users.name.toLowerCase().startsWith(sale.value.user.name.toLowerCase())
            });
        } else{
            filteredUsers.value = [];
        }
    }

    function selectedUser(user){
        sale.value.user = Object.assign({}, user);
        modal .value = false;
    }

    //Products
    function selectedProduct(item, add){
        if(sale.value.products.length == 0) {
          for(var i=0;i<productList.value.length;i++) {
            sale.value.products.push(Object.assign({}, productList.value[i]));
          }
          //sale.value.products = Object.assign([], productList.value);
          sale.value.products.forEach(element => { element.quantity = 0 });
          sale.value.total = 0;
          console.log(sale.value)
        }
        if (!sale.value.products.some(function(p) { if(p._id === item._id)
                                                      if(add) {p.quantity += 1; sale.value.total += parseInt(item.price); item.quantityAux++; updateProduct(item, add); return true }
                                                      else {p.quantity -= 1; sale.value.total -= parseInt(item.price); item.quantityAux--; updateProduct(item, add); return true}})) {
            
            var aux = Object.assign({}, item);
            item.quantityAux = 1;
            aux.quantity = 1;
            sale.value.total += aux.price;
            updateProduct(item, add)
            console.log(aux)
            sale.value.products.push(aux);
        }
      console.log(sale.value)
        
    }

    async function updateProduct(item, add){
      production.value.products.forEach(element => {
        if(element._id === item._id){
          if(add)
            element.quantity--;
          else 
            element.quantity++; 
        }
      });
    }

    async function getProducts() {
      productionService.getLast()
        .then(response => { 
          //productList.value = response.data[0].products;
          console.log(response)
          production.value = response.data[0];
          if(production.value != undefined)
            productList.value = response.data[0].products;
            
          getsale();
        })
        .catch(e => {
          console.log(e);
        });
        
    }
    getProducts();

    function closesale(){
      sale.value.finishedAt = Date();
      postsale();
    }

    function reopensale(){
      sale.value.finishedAt = {};
      postsale();
    }

    function deleteProduct(index){
        productList.value.splice(index, 1);
    }

    


    async function postsale() {
      if(!sale.value._id && sale.value.user.name != ''){
        sale.value.saleBy = firebase.auth().currentUser.email;
        sale.value.productionId = production.value._id;
        saleService.create(sale.value)
        .then(response => {
            saleList.value = response.data;
        })
        .catch(e => {
            console.log(e);
        });
      } else{
        saleService.update(sale.value._id, sale.value)
        .then(response => {
            console.log(response.data)
        })
        .catch(e => {
            console.log(e);
        });
      }

      productionService.update(production.value._id, production.value)
        .then(response => { 
          productList.value = response.data[0].products;
        })
        .catch(e => {
          console.log(e);
        });

      production.value.products.forEach(element => {
          element.quantityAux = 0;
      });  

      sale.value = {user: {}, products: []};   
    }
    

    function setsale(index){
      sale.value = saleList.value[index];
      productList.value.forEach(element => {
        sale.value.products.some(function(p) { 
                                    if(p._id === element._id)
                                      element.quantityAux = p.quantity;
                                  })
      })
      //productList.value = sale.value.products;
    }
    
    async function getsale() {
      if(!production.value.finishedAt.length) {
        saleService.get(production.value._id)
          .then(response => {
            saleList.value = response.data;
          })
          .catch(e => {
            console.log(e);
          });
      }
    }

     

    async function deletesale() {
      production.value.products.forEach(element => {
        var aux = sale.value.products.find(s => s._id === element._id);
        if(aux != undefined){
          element.quantity += aux.quantity;
        }
      });
      productionService.update(production.value._id, production.value)
      .then(response => { 
        productList.value = response.data[0].products;
      })
      .catch(e => {
        console.log(e);
      });
      saleService.delete(sale.value._id, sale.value.productionId)
        .then(response => {
          saleList.value = response.data;
        })
        .catch(e => {
          console.log(e);
        });
        sale.value = {user: {}, products: []}; 
    }

    function cancel() {
      sale.value = {user: {}, products: []};
      productList.value.forEach(element => {
        element.quantityAux = 0;
      })
    }

    // async function finish() {
    //   console.log('finish')
    //   // sale.value.finishAt = Date();
    //   // saleService.update(sale.value._id, sale.value)
    //   //   .then(response => {
    //   //       saleList.value = response.data;
    //   //   })
    //   //   .catch(e => {
    //   //       console.log(e);
    //   //   }); 
    //   //   sale.value = {user: {}, products: []};   
    // }

    return {
      sale,
      postsale,
      filteredUsers,
      filterUsers,
      setsale,
      deleteProduct,
      productList,
      saleList,
      deletesale,
      closesale,
      reopensale,
      selectedUser,
      getAllUsers,
      selectedProduct,
      getProducts,
      product,
      production,
      modal,
      cancel
    }
  }
}
</script>