<template>
  <section>
    <div class="container">
      <h1 class="title">
        Users
      </h1>
      <div class="post-form">
        <div class="field">
          <div class="control">
            <input v-model="user._id" class="input" type="text" placeholder="Id">
          </div>
        </div>
        <div class="field">
          <div class="control">
            <input v-model="user.name" class="input" type="text" placeholder="Nome">
          </div>
        </div>
        <div class="field">
          <div class="control">
            <input v-model="user.phone" class="input" type="text" placeholder="Telefone">
          </div>
        </div>
        <div class="field">
          <div class="control">
            <input v-model="user.address" class="input" type="text" placeholder="Endereço">
          </div>
        </div>
        <div class="field">
          <div class="control">
            <input v-model="user.facebook" class="input" type="text" placeholder="Facebook">
          </div>
        </div>
        <div class="field">
          <div class="control">
            <input v-model="user.instagram" class="input" type="text" placeholder="Instagram">
          </div>
        </div>
      </div>
      <br>
      <div class="field is-grouped">
        <div class="control">
          <button v-on:click="postUser()" class="button is-link">Submit</button>
        </div>
        <div class="control">
          <button v-on:click="user = {}" class="button is-link is-light">Cancel</button>
        </div>
         <div class="control">
          <button v-on:click="deleteUser()" class="button is-danger">Delete</button>
        </div>
      </div>

      <br>
      <div class="columns">
        <ul class="block-list is-small is-outlined is-success is-centered has-radius is-info">
          <div class="item" v-for="(item, index) in usersList" 
          :key="item.id"
          :index="index"
          @click="selectedUser(index)">
              <li class="text">{{ item.name }}</li>
          </div>
        </ul>
      </div>
    </div>
    
   
  </section>
</template>

<script>
import { ref } from 'vue';
import UserService from '@/services/UsersService.js';


export default {
  name: 'Items',
  setup() {
    const usersList = ref([]);
    const user = ref({});

    async function getAllUsers() {
      UserService.getAll()
        .then(response => {
          usersList.value = response.data;
        })
        .catch(e => {
          console.log(e);
        });
    }
    getAllUsers();

    function selectedUser(index){
      user.value = usersList.value[index];
    }

    async function postUser() {
      if(user.value.name.trim()){
        if(!user.value._id){
          UserService.create(user.value)
          .then(response => {
            usersList.value = response.data;
          })
          .catch(e => {
            console.log(e);
          });
        } else{
          UserService.update(user.value._id, user.value)
          .then(response => {
            console.log(response.data)
          })
          .catch(e => {
            console.log(e);
          });
        }
        user.value = {};   
      } else{
        alert('Fudeu')
      }
           
    }

    async function deleteUser() {
      UserService.delete(user.value._id)
        .then(response => {
          usersList.value = response.data;
        })
        .catch(e => {
          console.log(e);
        });
        user.value = {}; 
    }

    return {
      usersList,
      user,
      selectedUser,
      postUser,
      deleteUser
    }
  }
    
}
</script>

