<template>
  <section>
    <div class="container">
      <h1 class="title">
        Receitas
      </h1>
      <div class="post-form">
        <div class="field">
          <div class="control">
            <input v-model="recipe._id" class="input" type="text" placeholder="Id">
          </div>
        </div>
        <div class="field">
          <div class="control">
            <input v-model="recipe.description" class="input" type="text" placeholder="Descrição">
          </div>
        </div>
        <div class="field">
          <div class="control">
            <input v-model="recipe.quantity" class="input" type="text" placeholder="Rendimento">
          </div>
        </div>
        <div class="field">
          <div class="control">
            <input v-model="recipe.cost" class="input" type="text" placeholder="Custo">
          </div>
        </div>
        <div class="field">
          <div class="control">
            <input v-model="recipe.price" class="input" type="text" placeholder="Preço">
          </div>
        </div>
        <div class="field">
          <div class="control">
              <textarea v-model="recipe.directions" class="textarea" placeholder="Preparo" rows="5"></textarea>
          </div>
        </div>
        <div>
            <input v-model="itemInput.description" class="input" type="text" placeholder="Ingrediente" autocomplete="off" @input="filterItems">
            <div v-if="filteredItems">
                <ul>
                    <li v-for="filteredItem in filteredItems" :key="filteredItem._id" @click="setItem(filteredItem)">{{ filteredItem.description }}</li>
                </ul>
            </div>
        </div>
        <div class="field">
          <div class="control">
            <input v-model="itemInput.quantity" class="input" type="text" placeholder="Quantidade">
          </div>
        </div>
        <div class="control">
          <button v-on:click="insertIngredient()" class="button is-link">Adcionar</button>
        </div>
        <ul>
            <li v-for="(ingredient, index) in ingredientsList" :key="ingredient._id" :index="index" @dblclick="deleteIngredient(index)">{{ ingredient.description + ' => ' + ingredient.quantity }}</li>
        </ul>
      </div>
      <br>
      <div class="field is-grouped">
        <div class="control">
          <button v-on:click="postRecipe()" class="button is-link">Submit</button>
        </div>
        <div class="control">
          <button v-on:click="recipe = []; ingredientsList = [];" class="button is-link is-light">Cancel</button>
        </div>
         <div class="control">
          <button v-on:click="deleteRecipe()" class="button is-danger">Delete</button>
        </div>
      </div>

      <br>
      <div class="columns is-multiline">
          <div class="item column is-one-quarter" v-for="(recipe, index) in recipesList" :key="recipe.id" :index="index" @click="getRecipe(index)">
              <!-- <li class="text">{{ recipe.description }}</li> -->
              <div class="card">
                <div class="card-content">
                  <h2 class="is-size-4 has-text-weight-bold">{{ recipe.description }}</h2>
                </div>
              </div>
          </div>
      </div>
    </div>
    
   
  </section>
</template>

<script>
import { ref } from 'vue';
import recipesService from '@/services/RecipesService.js';
import itemsService from '@/services/ItemsService.js';

export default {
  name: 'Recipe',
  setup() {
    const recipesList = ref([]);
    const recipe = ref([]);
    const itemsList = ref([]);
    const filteredItems = ref([]);
    const itemInput = ref ({});
    const ingredientsList = ref([]);
    let auxCost = ref(0);

    function deleteIngredient(index){
      console.log(ingredientsList.value[index].cost)
      recipe.value.cost -= ingredientsList.value[index].cost;
      ingredientsList.value.splice(index, 1);
    }

    function insertIngredient(){
      if(isNaN(recipe.value.cost))
        recipe.value.cost = 0;
      recipe.value.cost += auxCost.value * itemInput.value.quantity;
      itemInput.value.cost = auxCost.value * itemInput.value.quantity;
      ingredientsList.value.push(itemInput.value);
      auxCost.value = 0;
      itemInput.value = {};
    }

    function filterItems(){
        if(itemInput.value.description != ''){
            filteredItems.value = itemsList.value.filter(items => {
                return items.description.toLowerCase().startsWith(itemInput.value.description.toLowerCase())
            });
        } else{
            filterItems.value = [];
        }
    }

    function setItem(item){
        itemInput.value.description = item.description;
        itemInput.value._id = item._id;
        if(isNaN(itemInput.value.cost))
          itemInput.value.cost = 0;
        //itemInput.value.cost += (item.price / item.quantity);
        auxCost.value += (item.price / item.quantity);
        filteredItems.value = [];
    }

    async function getAllRecipes() {
      recipesService.getAll()
        .then(response => {
          recipesList.value = response.data;
          //console.log(response.data);
        })
        .catch(e => {
          console.log(e);
        });
    }
    getAllRecipes();

    function getRecipe(index){
      recipe.value = recipesList.value[index];
      ingredientsList.value = recipe.value.ingredients;
      //console.log(ingredientsList.value)
    }

    async function postRecipe() {
      if(recipe.value.description.trim() && recipe.value.quantity.trim() && recipe.value.price.trim()){
        if(!recipe.value._id){
            recipe.value.ingredients = ingredientsList.value;   
            if(!recipe.value.ingredients){
                console.log(recipe.value.ingredients) 
                return alert('fuck');
            }
             
            recipesService.create(recipe.value)
            .then(response => {
                recipesList.value = response.data;
            })
            .catch(e => {
                console.log(e);
            });
        } else{
            recipe.value.ingredients = ingredientsList.value;
            recipesService.update(recipe.value._id, recipe.value)
            .then(response => {
                console.log(response.data)
            })
            .catch(e => {
                console.log(e);
            });
        }
        recipe.value = [];   
        ingredientsList.value = [];
      } else{
        alert('Fudeu')
      }
           
    }

    async function deleteRecipe() {
      recipesService.delete(recipe.value._id)
        .then(response => {
          recipesList.value = response.data;
        })
        .catch(e => {
          console.log(e);
        });
        recipe.value = []; 
        ingredientsList.value = [];
    }
    
    async function getAllItems() {
      itemsService.getAll()
        .then(response => {
          itemsList.value = response.data;
          //console.log(response.data);
        })
        .catch(e => {
          console.log(e);
        });
    }
    getAllItems();

    

    return {
      recipesList,
      itemsList,
      recipe,
      getRecipe,
      postRecipe,
      deleteRecipe,
      itemsService,
      filteredItems,
      filterItems,
      itemInput,
      setItem,
      insertIngredient,
      deleteIngredient,
      ingredientsList
    }
  },
  methods: {
    
  }
}
</script>